.page-footer {
  background-color: #292929;

  font-size: 18px;
  font-weight: 400;

  color: #ffffff;

  text-align: center;

  padding-top: 100px;
  padding-bottom: 25px;
  margin-top: -120px;

  @media (min-width: $tablet-width) {
    margin-top: 0;
    padding-top: 70px;
  }
}

.page-footer__wrapper {
  width: 320px;

  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 640px;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    width: 960px;

    justify-content: flex-start;
  }
}

.contacts {
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    width: 370px;
    order: 1;

    text-align: left;

    padding: 0;
  }

  @media (min-width: $desktop-width) {
    width: 325px;
    margin-right: 55px;
  }

  &__title {
    font-weight: 700;
    line-height: 27px;

    @media (min-width: $tablet-width) {
      text-transform: uppercase;
    }
  }

  &__text {
    line-height: 27px;

    @media (min-width: $tablet-width) {
      margin-bottom: 5px;
    }
  }

  &__phone {
    font-size: 18px;
    font-weight: 700;
    line-height: 27px;

    color: #ffffff;
    border: 1px solid #3f3f3f;

    text-decoration: none;

    margin-bottom: 35px;

    @media (min-width: $tablet-width) {
      display: inline;
      border: none;

      padding: 0;
    }

    &--mobile {
      display: none;

      @media (min-width: $tablet-width) {
        display: inline-block;

        margin-right: 10px;
      }
    }
  }
}

.social {
  @media (min-width: $tablet-width) {
    order: 3;

    width: 100%;

    margin-top: 50px;
  }

  @media (min-width: $desktop-width) {
    width: 200px;
    order: 2;

    margin: 0;
  }

  &__title {
    display: none;

    font-weight: normal;

    @media (min-width: $desktop-width) {
      display: block;

      margin-bottom: 25px;
    }
  }
}

.social__list {
  list-style: none;

  font-size: 0;

  display: flex;
  flex-wrap: wrap;

  border: 1px solid #3f3f3f;

  margin-bottom: 35px;

  @media (min-width: $desktop-width) {
    justify-content: center;
    border: none;
  }
}

.social__item {
  width: 33.33%;

  border-right: 1px solid #3f3f3f;

  @media (min-width: $desktop-width) {
    width: 50px;
    height: 50px;

    border: 1px solid #3f3f3f;

    margin: 0 3px;
  }

  &:last-child {
    border-right: none;

    @media (min-width: $desktop-width) {
      border-right: 1px solid #3f3f3f;
    }
  }
}

.social__link {
  display: block;

  background-repeat: no-repeat;
  background-position: center;

  padding-top: 35px;
  padding-bottom: 35px;

  @media (min-width: $desktop-width)  {
    width: 50px;
    height: 50px;

    padding: 0;
  }
}

.social__link--vkontakte {
  background-image: url("../img/vk-icon.png");

  @media (min-width: $desktop-width) {
    background-size: 15px;
  }
}

.social__link--facebook {
  background-image: url("../img/facebook-icon.png");

  @media (min-width: $desktop-width) {
    background-size: 10px;
  }
}

.social__link--instagram {
  background-image: url("../img/insta-icon.png");

  @media (min-width: $desktop-width) {
    background-size: 15px;
  }
}

.copyright {
  @media (min-width: $tablet-width) {
    order: 2;
    width: 170px;
  }

  @media (min-width: $desktop-width) {
    width: 165px;
    order: 3;

    margin-left: auto;
  }

  &__text {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;

    margin-bottom: 15px;
  }

  &__link {
    margin: 0 auto;
    margin-top: 15px;

    border: 1px solid #3f3f3f;

    font-size: 18px;
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;

    color: #ffffff;

    width: 160px;

    @media (min-width: $desktop-width) {
      margin-top: 25px;
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }
}

// Fix style for ie11
.ie11 .page-footer {
  margin-top: 0;
  padding-top: 50px;
  padding-bottom: 50px;
}

.ie11 .copyright__link,
.ie11 .contacts__phone {
  text-decoration: none;
}