/* File blocks/modules/advantages/advantages.scss */
@import "./node_modules/slick-carousel/slick/slick.scss";

.advantages {
  position: relative;
  width: 100%;
  height: auto;
  
  clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 80%);
  -webkit-clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 80%);

  padding-top: 30px;
  padding-bottom: 160px;

  background-color: #242424;

  @media (min-width: $tablet-width) {
    padding-top: 50px;
    padding-bottom: 190px;

    clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 85%);
    -webkit-clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 85%);
  }

  @media (min-width: $desktop-width) {
    padding-top: 80px;
    padding-bottom: 150px;

    clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 90%);
    -webkit-clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 90%);
  }
}
.advantages__wrapper {
  width: 320px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 640px;
  }
  @media (min-width: $desktop-width) {
    width: 960px;
  }
}
.advantages__list {
  list-style: none;

  @media (min-width: $tablet-width) {
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.advantages__item {
  text-align: center;
  padding: 20px;

  position: relative;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    display: flex;
    align-items: center;

    text-align: left;
  }
  @media (min-width: $desktop-width) {
    width: 33.33%;
    flex-direction: column;
    text-align: center;

    padding-left: 40px;
    padding-right: 40px;
  }

  &::before {
    @media (min-width: $tablet-width) {
      content: "";

      position: absolute;
      bottom: 0;
      left: 20px;
      right: 20px;

      width: 600px;
      height: 1px;

      background-color: #ffffff;

      opacity: 0.1;
    }

    @media (min-width: $desktop-width) {
      top: 20px;
      bottom: 0;
      left: 0;

      width: 1px;
      height: 300px;
    }
  }

  &:last-child::before {
    @media (min-width: $tablet-width) {
      display: none;
    }
    @media (min-width: $desktop-width) {
      display: block;
    }
  }

  &:first-child::before {
    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}

.advantages__icon {
  display: block;
  margin-top: 22px;

  @media (min-width: $tablet-width) {
    width: 185px;
    margin-top: 0;
  }

  & img {
    margin: 0 auto;
  }
}

.advantages__text {
  @media (min-width: $tablet-width) {
    padding-left: 35px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 0;
  }
}

.advantages__title {
  color: #f7f4f1;

  font-size: 30px;
  font-weight: 400;
  line-height: 42px;
  text-transform: uppercase;

  margin-top: 28px;
  margin-bottom: 22px;

  @media (min-width: $tablet-width) {
    line-height: 24px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  @media (min-width: $desktop-width) {
    margin-top: 39px;
    margin-bottom: 21px;
  }
}

.advantages__description {
  color: #f7f4f1;
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;

  @media (min-width: $tablet-width) {
    line-height: 24px;
  }
  @media (min-width: $desktop-width) {
    line-height: 27px;
  }
}

.slick-dots {
  display: flex;
  justify-content: center;

  list-style: none;

  margin-top: 15px;
  margin-bottom: 15px;

  & li {
    margin-left: 23px;
    padding: 5px;

    &:first-child {
      margin-left: 0;
    }
  }

  & button {
    display: block;
    font-size: 0;

    width: 10px;
    height: 10px;

    border: 1px solid #fff;
    background-color: transparent;

    transform: rotate(45deg);

    padding: 0;

    cursor: pointer;
  }
}

.slick-active {

  & button {
    background-color: #ffffff;
  }
}

// Fix style for ie11
.ie11 .advantages {
  padding-bottom: 50px;

  @media (min-width: $desktop-width) {
    padding-top: 0;
    padding-bottom: 70px;
    margin-top: -100px;
  }
}

.ie11 .advantages__text {
  @media (min-width: $desktop-width) {
    width: 100%;
  }
}