.modal {
  display: none;

  position: fixed;
  z-index: 11;

  color: #000000;

  background-color: #f8f3f0;
  background-image: url("../img/pattern-light.jpg");
  background-position: 0 0;
  background-repeat: repeat;
  box-shadow: 0 30px 50px rgba( 0, 0, 0, 0.7);
}

.modal-overlay {
  display: none;
  
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

.modal-overlay--show {
  display: block;
}

.modal-login {
  top: 120px;
  left: 50%;

  margin-left: -230px;

  width: 300px;
  padding: 50px 80px;
}

.modal-login h2 {
  margin: 0;
  margin-bottom: 20px;

  font-size: 30px;
  line-height: 42px;
}

.modal__description {
  margin: 0;
  margin-bottom: 10px;
}

.modal--show {
  display: block;
}

.modal--error {
  animation-name: shake;
  animation-duration: 0.6s;
}

.modal-close {
  box-sizing: border-box;

  position: absolute;
  top: 0;
  right: -35px;

  width: 22px;
  height: 22px;

  font-size: 0;

  background-color: transparent;
  border: 0;

  cursor: pointer;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 10px;
    left: 2px;

    width: 19px;
    height: 3px;

    background-color: #d0d0d0;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.login-form {
  & p {
    margin: 0;
    margin-bottom: 10px;
  }

  & input[type="text"],
  & input[type="password"] {
    box-sizing: border-box;
    width: 300px;
    padding: 10px 15px;
    padding-right: 40px;

    font: inherit;
    color: #000000;
    text-transform: uppercase;

    background-color: #f9f6f3;
    border: 1px solid #e5ddd5;
  }

  & input:focus {
    border-color: #663d15;
  }

  & input::placeholder {
    color: #000000;
  }

  & .login-help {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    & a {
      font: inherit;
      color: #000000;
      text-transform: uppercase;
      text-decoration: none;
    }
  }

  & > .button {
    width: 100%;
    box-sizing: border-box;

    background-color: transparent;
    border: 1px solid #e5ddd5;

    text-transform: uppercase;

    cursor: pointer;
  }
}

.login-icon-user {
  background-image: url("../img/user.svg");
  background-position: 270px center;
  background-repeat: no-repeat;
}

.login-icon-password {
  background-image: url("../img/lock.svg");
  background-position: 270px center;
  background-repeat: no-repeat;
}

.login-checkbox {
  position: relative;

  padding-left: 30px;

  cursor: pointer;

  &:hover,
  &:focus {
      color: #663d15;
  }

  & input[type="checkbox"] + .checkbox-indicator {
    position: absolute;
    top: 3px;
    left: 0;

    width: 17px;
    height: 17px;

    border: 1px solid #e5ddd5;
  }

  & input[type="checkbox"]:focus + .checkbox-indicator {
    border-color: #663d15;
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
  }

  & input[type="checkbox"]:checked + .checkbox-indicator::before,
  & input[type="checkbox"]:checked + .checkbox-indicator::after {
    content: "";

    position: absolute;
    top: 8px;
    left: 1px;

    width: 15px;
    height: 2px;

    background-color: #000000;
  }

  & input[type="checkbox"]:checked + .checkbox-indicator::before {
    transform: rotate(45deg);
  }

  & input[type="checkbox"]:checked + .checkbox-indicator::after {
    transform: rotate(-45deg);
  }
}