/* File blocks/modules/main-nav/main-nav.scss */
.main-nav {
  width: auto;
  min-height: 51px;

  background-color: #826550;

  position: relative;

  @media (min-width: $tablet-width) {
    width: auto;
    margin: 0;

    background-color: #000000;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;

  display: block;
  width: 70px;
  height: 50px;

  background-color: #6a4d38;

  cursor: pointer;
  border: none;

  @media (min-width: $tablet-width) {
    display: none;
  }

  &:hover,
  &:active {
    background-color: #624530;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";

  position: absolute;
  top: 15px;
  left: 17px;

  width: 36px;
  height: 2px;

  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 0;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 24px;
    left: 18px;

    width: 36px;
    height: 2px;

    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav__wrapper {
  @media (min-width: $tablet-width) {
    width: 84%;
    max-width: 960px;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
  }
  @media (min-width: $desktop-width) {
    // width: 960px;
  }
}

.main-nav__list {
  @include reset-list;
}

.main-nav--closed .site-list__item {
  @media (max-width: $mobile-width-only) {
    display: none;
  }

  &--active {
    @media (max-width: $mobile-width-only) {
      display: block;

      & a {
        padding-right: 70px;
        padding-left: 70px;

        text-align: center;
      }
    }
  }
}

.main-nav--closed .user-list__item {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.main-nav--opened .main-nav__wrapper {
  @media (max-width: $mobile-width-only) {
    position: absolute;
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;

    background-color: #6a4d38;
  }
}

.main-nav--opened .main-nav__wrapper::before {
  @media (max-width: $mobile-width-only) {
    content: "";

    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    width: 7px;
    height: 100%;

    box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, 0.4);
  }
}

.main-nav--nojs .main-nav__toggle {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}

.main-nav--nojs .main-nav__wrapper {
  @media (max-width: $mobile-width-only) {
    position: static;
    min-height: 0;
  }
}

.main-nav--nojs .site-list__item {
  @media (max-width: $mobile-width-only) {
    display: block;
  }
}

.main-nav--nojs .site-list__item--active a {
  @media (max-width: $mobile-width-only) {
    padding-right: 21px;
    padding-left: 21px;

    text-align: left;
  }
}

.main-nav--nojs .user-list__item {
  @media (max-width: $mobile-width-only) {
    display: block;

    background-color: #6a4d38;
  }
}

.site-list {
  font-weight: bold;

  & a {
    display: block;
    padding: 12px 21px;
    padding-bottom: 13px;

    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;

    background-color: #826550;
    border-top: 1px solid #886e5b;
    border-bottom: 1px solid #71543f;

    @media (min-width: $tablet-width) {
      border: none;
      background-color: transparent;
      padding: 29px 15px;

      &:hover {
        color: #826550;
      }
    }

    @media (min-width: $desktop-width) {
      padding: 29px 21px;

      &:hover {
        color: #000000;
        background-color: #ffffff;
      }
    }
  }

  @media (min-width: $tablet-width) {
    width: 570px;

    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    flex-grow: 1;
  }
  @media (min-width: $desktop-width) {
    width: 780px;
  }
}
  
.user-list {
  @media (min-width: $tablet-width) {
    width: 60px;
  }
  @media (min-width: $desktop-width) {
    width: 129px;
  }
}

.user-list a[href] {
  @media (min-width: $tablet-width) {
    font-size: 0;
  }

  @media (min-width: $desktop-width) {
    font-size: 18px;
    text-transform: uppercase;
  }

  &:hover {
    background-color: #755a46;
    border-top: #755a46;

    @media (min-width: $tablet-width) {
      background-color: transparent;
    }
    @media (min-width: $desktop-width) {
      color: #000000;
      background-color: #ffffff;
    }
  }

  &:active {
    color: rgba(255, 255, 255, 1);
    background-color: #6a4d38;
    border-top: #6a4d38;

    @media (min-width: $tablet-width) {
      background-color: transparent;
    }
  }
}

.user-list__login {
  position: relative;

  display: block;
  padding: 17px 21px;
  padding-left: 49px;

  line-height: 18px;
  color: #ffffff;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    padding: 32px 21px;

    &:hover .user-list__login-icon {
      opacity: 1;

      // polygon {
      //   fill: #826550;
      // }

      fill: #826550;
    }
    &:active .user-list__login-icon {
      opacity: 0.3;

      // polygon {
      //   fill: #ffffff;
      // }

      fill: #ffffff;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 32px 21px 32px 62px;

    &:hover .user-list__login-icon {
      opacity: 0.3;

      // polygon {
      //   fill: #000000;
      // }

      fill: #000000;
    }
    &:active .user-list__login-icon {
      opacity: 0.3;

      // polygon {
      //   fill: #000000;
      // }

      fill: #000000;
    }
  }
}

.user-list__login-icon {
  position: absolute;
  top: 17px;
  left: 21px;

  width: 16px;
  height: 16px;

  opacity: 0.3;

  // polygon {
  //   fill: #ffffff;
  // }

  fill: #ffffff;

  @media (min-width: $tablet-width) {
    top: 32px;
    left: 24px;
  }

  & polygon {
    fill: #ffffff;
    opacity: 0.3;
  }
}

// Fix style for ie11
.ie11 .user-list a,
.ie11 .site-list a {
  text-decoration: none;
}