.page-header__logo {
  height: 85px;

  text-align: center;

  background-color: #242424;

  @media (min-width: $tablet-width) {
    order: 2;

    width: 100%;
    height: 400px;

    background-color: #000000;
    background-image: url("../img/bg-header.jpg");
    background-repeat: no-repeat;
    background-position: center 0;
  }

  @media (min-width: $desktop-width) {
    height: 648px;
    background-position: center 0;
    background-size: cover;
  }
}

.page-header__logo svg {
  width: 226px;
  height: 85px;

  @media (min-width: $tablet-width) {
    display: block;
    width: 370px;
    height: auto;
    margin: 0 auto;
    margin-top: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 57px;
  }
}

.page-header__logo-barbershop {
  display: none;

  @media (min-width: $tablet-width) {
    display: inline-block;
  }
}

.page-header__logo-mustache{
  display: none;

  @media (min-width: $desktop-width) {
    display: inline-block;
  }
}

.ie11 .page-header__logo {
  @media (min-width: $tablet-width) {
    min-height: 600px;
  }
}

// Fix style for ie11
.ie11 .page-header__logo svg {
  margin-top: -100px;
}