/* File blocks/modules/stats/stats.scss */
.stats {
  background-color: #242424;
}

.stats__wrapper {
  width: 320px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 640px;
    margin-top: -173px;
  }
  @media (min-width: $desktop-width) {
    display: flex;

    width: 960px;
    margin-top: -160px;
  }
}

.stats__header {
  padding-top: 30px;
  padding-right: 40px;
  padding-bottom: 25px;
  padding-left: 40px;

  color: #000000;

  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    position: relative;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    min-height: 129px;

    &::before {
      content: "";

      position: absolute;
      top: 27px;
      left: -15px;

      border: 15px solid transparent;
      border-bottom-color: #ffffff;
      transform: rotate(45deg);
    }
  }

  @media (min-width: $desktop-width) {
    display: block;
    width: 320px;

    &::before {
      top: 22px;
    }
  }
}

.stats__slogan {
  display: block;
  margin-bottom: 15px;

  font-size: 30px;
  line-height: 30px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: 240px;
    margin-bottom: 18px;

    font-size: 40px;
    line-height: 40px;
    text-align: left;
    text-transform: none;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    margin-bottom: 42px;
  }
}

.stats__intro {
  margin: 0;

  font-size: 20px;
  line-height: 27px;
  text-align: center;

  @media (min-width: $tablet-width) {
    width: 210px;
    margin-top: 5px;
    margin-right: 47px;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: auto;
    margin-bottom: 32px;
    margin-right: 30px;
  }
}

.stats__legend {
  display: block;

  font-size: 20px;
  line-height: 20px;
}

.stats__legend--top {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    width: 240px;

    color: rgba(0, 0, 0, 0.3);
  }

  @media (min-width: $desktop-width) {
    width: auto;
  }
}

.stats__legend--bottom {
  padding-top: 20px;
  padding-bottom: 14px;

  text-align: center;
  color: rgba($color: #ffffff, $alpha: 0.3);

  background-color: $bg-darkgray-primary;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.stats__legend sup {
  top: -0.25em;

  font-size: 90%;
}

.stats__list {
  width: 100%;

  border-collapse: collapse;

  @media (min-width: $tablet-width) {
    display: flex;

    & tbody {
      display: flex;
      flex-wrap: wrap;
    }

    & tr {
      width: 320px;
      padding: 25px 35px;
      padding-bottom: 28px;
    }

    & td {
      display: block;
      width: auto;
      padding: 0;
    }
  }
  @media (min-width: $desktop-width) {
    display: flex;
    width: 640px;
  }
}

.stats__list tr {
  color: #ffffff;

  background-color: #404040;

  &:nth-child(2) {
    background-color: #4e4e4e;
  }

  &:nth-child(3) {
    background-color: #353535;
  }

  &:nth-child(4) {
    background-color: #404040;
  }
}

.stats__value,
.stats__field {
  width: 50%;
  padding: 20px;
  padding-bottom: 19px;

  vertical-align: top;
}

.stats__value {
  padding-right: 0;

  font-size: 40px;
  line-height: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 7px;

    font-size: 72px;
    line-height: 72px;
  }
}

.stats__value sup {
  top: -0.35em;

  margin-left: 5px;
}

.stats__field {
  padding-left: 0;

  font-size: 18px;
  line-height: 18px;
  text-align: right;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 27px;
    text-align: left;

    & br {
      display: none;
    }
  }
}

// Fix style for ie11
.ie11 .stats {
  @media (min-width: $tablet-width) {
    position: relative;
    min-height: 350px;
  }
}
.ie11 .stats__wrapper {
  @media (min-width: $tablet-width) {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

.ie11 .stats__list tr {
  @media (min-width: $tablet-width) {
    float: left;
  }
}