.news {
  margin-top: -70px;

  border-bottom: 1px solid #e5ddd5;

  @media (min-width: $desktop-width) {
    margin-top: 25px;
  }
}

.news__wrapper {
  width: 320px;

  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 640px;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding: 0;
  }
  @media (min-width: $desktop-width) {
    width: 960px;
  }
}

.news__title {
  font-size: 30px;
  font-weight: bold;
  line-height: 30px;
  text-transform: uppercase;

  text-align: center;

  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    order: 1;

    font-size: 40px;
  }
}

.news__list {
  list-style: none;

  @media (min-width: $tablet-width) {
    order: 3;

    display: flex;
    flex-wrap: wrap;

    margin-top: 60px;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }
}

.news__item {
  display: flex;
  flex-wrap: wrap;

  position: relative;

  @media (min-width: $tablet-width) {
    width: 300px;
  }

  @media (min-width: $desktop-width) {
    width: 33.33%;
  }

  &::before {
    content: "";

    position: absolute;
    bottom: 25px;

    width: 280px;
    height: 1px;
    background-color: #e5ddd5;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }

  &:last-child::before {
    content: none;
  }

  &:nth-child(3) {
    display: none;

    @media (min-width: $desktop-width) {
      display: flex;
    }
  }
}

.news__date {
  position: relative;

  display: block;
  width: 50px;
  height: 50px;

  color: #ffffff;
  background-color: #000000;

  font-size: 16px;
  font-weight: 400;
  line-height: 15px;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    bottom: -35px;
    left: 0;
    border: 25px solid transparent;	
    border-top: 10px solid #000000;
  }
}

.news__day {
  display: block;

  font-size: 30px;
  font-weight: 400;
  line-height: 30px;

  padding-top: 3px;
}

.news__text {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;

  width: 210px;

  padding-left: 20px;
  margin: 0;
  margin-top: -10px;
  margin-bottom: 45px;

  @media (min-width: $tablet-width) {
    width: 250px;

    padding-right: 20px;
  }
}

.news__to-all {
  font-size: 18px;
  font-weight: bold;
  line-height: 24px;
  text-transform: uppercase;

  color: #000000;

  border: 1px solid #e5ddd5;

  margin-bottom: 28px;

  @media (min-width: $tablet-width) {
    order: 2;

    width: 160px;

    line-height: 15px;
  }
}

// Fix style for ie11
.ie11 .news {
  margin-top: 50px;
}

.ie11 .news__to-all {
  text-decoration: none;
}