/* File blocks/modules/reviews/reviews.scss */
@import "./node_modules/slick-carousel/slick/slick.scss";

.reviews {
  position: relative;
  width: 100%;
  height: auto;

  clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 83%);
  -webkit-clip-path: polygon(0 75%, 0 0, 100% 0, 100% 75%, 50% 83%);

  padding-bottom: 100px;

  background-color: #f8f5f2;

  @media (min-width: $tablet-width) {
    clip-path: polygon(0 85%, 0 0, 100% 0, 100% 85%, 50% 85%);
    -webkit-clip-path: polygon(0 85%, 0 0, 100% 0, 100% 85%, 50% 85%);

    padding-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 120px;
    padding-top: 15px;
  }
}

.reviews__wrapper {
  width: 320px;

  margin: 0 auto;

  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    width: 640px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    padding-right: 0;
    padding-left: 0;
  }

  @media (min-width: $desktop-width) {
    width: 960px;
  }
}

.reviews__write {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    width: 160px;

    color: #000000;
    border: 1px solid #e5ddd5;

    font-size: 18px;
    font-weight: 700;
    line-height: 15px;
    text-transform: uppercase;
  }
}

.reviews__title {
  font-size: 30px;
  font-weight: 700;
  line-height: 30px;

  text-transform: uppercase;
  text-align: center;

  margin-top: 30px;
  margin-bottom: 37px;

  @media (min-width: $tablet-width) {
    font-size: 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 47px;
  }
}

.reviews__list {
  @media (min-width: $tablet-width) {
    width: 100%;

    padding-left: 50px;
    padding-right: 50px;
    padding-top: 38px;
    padding-bottom: 38px;

    position: relative;

    border-top: 1px solid #e5ddd5;
    border-right: 1px solid #e5ddd5;
    border-left: 1px solid #e5ddd5;
  }

  @media (min-width: $desktop-width) {
    border: none;

    padding-top: 0;
    padding-bottom: 0;
  }

  &::before,
  &::after {
    @media (min-width: $tablet-width) {
      content: "";
      display: block;
      width: 230px;
      height: 0px;

      position: absolute;
      top: 100%;

      border-bottom: 1px solid #e5ddd5;
    }

    @media (min-width: $desktop-width) {
      content: none;
    }
  }
  &::before {
    @media (min-width: $tablet-width) {
      left: -1px;
    }
  }
  &::after {
    @media (min-width: $tablet-width) {
      right: -1px;
    }
  }
}

.slick-slide {
  @media (min-width: $desktop-width) {
    margin-left: 20px;
    margin-right: 20px;

    border: 1px solid #e5ddd5;
  }
}

.slick-arrow {
  position: absolute;
  top: 0;

  width: 50px;
  height: 100%;

  border: none;

  font-size: 0;

  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center center;

  @media (min-width: $desktop-width) {
    border: 1px solid #e5ddd5;
  }
}

.slick-prev {
  left: 0;

  border-right: 1px solid #e5ddd5;

  background-image: url("../img/left-arrow.png");
}

.slick-next {
  right: 0;

  border-left: 1px solid #e5ddd5;

  background-image: url("../img/right-arrow.png");
}

.inner-testimonial {
  display: flex;
  flex-wrap: wrap;
}

.reviws__author-picture {
  display: block;
  width: 50px;
  height: 50px;

  position: relative;
  overflow: hidden;

  margin: 0;
  margin-top: 10px;

  @media (min-width: $tablet-width) {
    width: 83px;
    height: 83px;

    border-radius: 50%;

    margin-left: 30px;
    margin-right: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 249px;
    height: 166px;

    border-radius: 0;

    margin-left: 0;
    margin-right: 50px;
    margin-top: 0;
    margin-bottom: 0;
  }
}

.reviews__author-image {
  position: absolute;
  left:50%;
  top:50%;
  transform: translate(-50%, -50%);
  max-width: 150%;
  max-height: 150%;

  @media (min-width: $desktop-width) {
    max-width: 100%;
    max-height: 100%;
  }
}

.reviews__text {
  width: 190px;
  flex-grow: 1;

  padding-left: 20px;

  @media (min-width: $tablet-width) {
    padding-left: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 35px;
  }
}

.reviews__author-name {
  font-size: 24px;
  font-weight: 700;
  line-height: 27px;
  font-style: normal;
}

.reviews__desc {
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.reviews .slick-dots {
  @media (min-width: $tablet-width) {
    position: absolute;
    left: 50%;
    bottom: -25px;

    margin-left: -53px;
  }

  @media (min-width: $desktop-width) {
    bottom: -75px;
  }

  & button {
    border: 1px solid #000000;

    cursor: pointer;
  }
}

.reviews .slick-active {
  & button {
    background-color: #000000;
  }
}

// Fix style for ie11
.ie11 .reviews {
  padding-bottom: 40px;

  @media (min-width: $desktop-width) {
    padding-bottom: 100px;
  }
}

.ie11 .reviews__write {
  text-decoration: none;
}